import React, { Component } from 'react'
import './index.css'

class FilterBar extends Component {
  constructor (props) {
    super(props)
    this.state = {
      searchStatus: ''
    }
  }
  toggleSearchStatus = (searchStatus) => {
    this.setState({
      searchStatus
    })
  }
  onFocus = () => {
    if (this.state.searchStatus) {
      return
    }
    const that = this
    this.toggleSearchStatus(true)
    document.addEventListener('click', function handleClick(e) {
      console.log(e)
      // document.removeEventListener('click', handleClick)
    })
  }
  click = () => {
    console.log(123)
    this.toggleSearchStatus(false)
  }
  render () {
    const {filterValue, onInput, toggleType, type} = this.props
    const {searchStatus} = this.state
    return (
      <div className="filter-bar">
        <div className="filter-bar__toggle-btn" onClick={toggleType}>
          <svg className={`icon ${type}-icon`} aria-hidden="true">
            <use href={`#icon-${type}`}></use>
          </svg>
        </div>
        <div className="filter-bar__input-warp">
          <input red="input" type="text"
            className={'search-input ' + (searchStatus ? ' focus' : '')}
            value={filterValue} onInput={onInput}
            onFocus={this.onFocus}
          />
          <svg className="icon search-icon" aria-hidden="true">
            <use href="#icon-search"></use>
          </svg>
          <svg className="icon clear-icon" onClick={this.click} aria-hidden="true">
            <use href="#icon-clear11"></use>
          </svg>
        </div>
      </div>
    )
  }
}

export default FilterBar



